export default{
	data(){
		return{
			list:[{
				name:'物流消息',
				image:'../../../static/informa.png',
			},{
				name:'聊天记录',
				image:'../../../static/informb.png',
			},{
				name:'系统通知',
				image:'../../../static/informc.png',
			},{
				name:'入驻通知',
				image:'../../../static/informd.png',
			}]
		}
	},
	onLoad() {
		var that = this
		uni.getSystemInfo({
		    success: function (res) {
				that.windowHeight = res.windowHeight
		        console.log('屏幕高度为'+res.windowHeight);
		    }
		});
	},
	methods:{
		//点击消息
		informClick(key){
			if(key == 0){
				//物流消息
				uni.navigateTo({
					url:'Logistics/logistics'
				})
			}if(key == 1){
				//聊天记录
				uni.navigateTo({
					url:'Chat/chat'
				})
			}if(key == 2){
				//系统通知
				uni.navigateTo({
					url:'System/system'
				})
			}if(key == 3){
				//入驻通知
				uni.navigateTo({
					url:'Enter/enter'
				})
			}
		},
	}
}
